import {
  Box,
  Button,
  Flex,
  HStack,
  Image,
  Input,
  InputGroup,
  InputLeftElement,
  SimpleGrid,
  Spacer,
  Stack,
  Table,
  TableContainer,
  Tbody,
  Td,
  Text,
  Th,
  Thead,
  Tr,
} from "@chakra-ui/react";

import totalleads from "../../icons/totalleads.svg";
import openleads from "../../icons/openleads.svg";
import finalleads from "../../icons/finalleads.svg";
import hotleads from "../../icons/hotleads.svg";
import unsubscribe from "../../icons/unsubscribe.svg";
import wronglead from "../../icons/wronglead.svg";
import enrolled from "../../icons/enrolled.svg";

import "react-date-range/dist/styles.css";
import "react-date-range/dist/theme/default.css";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getData } from "../../redux/actions/common.action";
import Loader from "../../components/Loader";
import { SearchIcon } from "@chakra-ui/icons";
import axios from "axios";

import { BASE_URL } from "../../config/RequestMethod";
import { TfiEye } from "react-icons/tfi";
import Cookies from "js-cookie";

const UserDashboard = () => {
  const { leadscount } = useSelector((state: any) => state.common);

  console.log(
    leadscount && leadscount[0]?.totalLeads,
    "userdash",
    leadscount?.enrolledPrice
  );
  const leadsData = [
    {
      icons: totalleads,
      name: "Total Leads",
      amount: leadscount && leadscount?.totalPrice,
      numofLeads: leadscount && leadscount?.totalLeads,
      color: "#37AE00",
    },
    {
      icons: openleads,
      name: "Open Leads",
      amount: leadscount && leadscount?.openPrice,
      numofLeads: leadscount && leadscount?.openLeads,
      color: "#E27A00",
    },
    {
      icons: finalleads,
      name: "Final Leads",
      amount: leadscount && leadscount?.finalPrice,
      numofLeads: leadscount && leadscount?.finalLeads,
      color: "#1243A0",
    },
    {
      icons: hotleads,
      name: "Hot Leads",
      amount: leadscount && leadscount?.hotPrice,
      numofLeads: leadscount && leadscount?.hotLeads,
      color: "#5B5B60",
    },
    {
      icons: unsubscribe,
      name: "Unsubscribed",
      amount: leadscount && leadscount?.unsubscribePrice,
      numofLeads: 0,
      color: "#FFBF00",
    },
    {
      icons: wronglead,
      name: "Wrong Leads",
      amount: leadscount && leadscount?.wrongPrice,
      numofLeads: leadscount && leadscount?.wrongLeads,
      color: "#FF0000",
    },
    {
      icons: enrolled,
      name: "Enrolled",
      amount: leadscount && leadscount?.enrolledPrice,
      numofLeads: leadscount && leadscount?.enrolledLeads,
      color: "#8580C2",
    },
  ];

  const dispatch: any = useDispatch();
  useEffect(() => {
    dispatch(getData("dashboard/leads-count"));
  }, [dispatch]);

  const [filteredData, setFilteredData] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const [search, setSearch] = useState<string>("");
  const [limit, setLimit] = useState<number>(20);
  //  console.log(filteredData,"ok")

  const url = `${BASE_URL}lead/follow-up-leads`;

  const handleFilterSubmit = async (): Promise<void> => {
    try {
      setIsLoading(true);
      const token = Cookies.get("token"); // Get the token from the cookie

      const res = await axios.get(url, {
        headers: {
          Authorization: `Bearer ${token}`, // Pass the token in the headers
        },
      });
      setFilteredData(res.data.Data);
      setTotalPages(res.data.totalPages);
      setIsLoading(false);
    } catch (error) {
      setIsLoading(false);
      console.error(error);
    }
  };

  useEffect(() => {
    handleFilterSubmit();
  }, [page, limit]);
  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(event.target.value);
  };
  const handlePageChange = (pageNumber: number) => {
    if (pageNumber < 1 || pageNumber > totalPages) {
      return;
    }
    setPage(pageNumber);
  };

  return (
    <Stack>
      <SimpleGrid columns={{ base: 2, sm: 2, md: 3, lg: 4 }} gap="0.5rem">
        {leadsData?.map((el, i) => (
          <Box
            // w="19.188rem"
            h="3.7rem"
            px="4"
            py="1"
            borderRadius={"1.1rem"}
            bg="whiteAlpha.900"
          >
            <HStack>
              <Box>
                <Text
                  fontSize={"0.9rem"}
                  padding={1}
                  // ml="1.125rem"
                  fontWeight={"800"}
                  color={el.color}
                >
                  ₹ {el.amount}
                </Text>
                <Text
                  fontSize={"0.95rem"}
                  // mt={"0.4rem"}
                  // ml="1.125rem"
                  fontWeight={600}
                >
                  {el.name}{" "}
                  <span style={{ fontWeight: "400", marginTop: "8px" }}>
                    {el.numofLeads}
                  </span>{" "}
                </Text>
              </Box>
              <Spacer />
              <Box mt="0">
                <Image
                  mt={"0"}
                  borderRadius="full"
                  boxSize="40px"
                  src={el.icons}
                  alt="error Icons"
                />
              </Box>
            </HStack>
          </Box>
        ))}
      </SimpleGrid>

      <Stack py="1" bg="whiteAlpha.900" borderRadius={"0.25rem"}>
        <Box
          px="4"
          display={"flex"}
          justifyContent={"space-between"}
          alignItems={"center"}
        >
          <Flex w="100%" textAlign={"center"}>
            <Text mr="2" mt="0.5" fontWeight={700} fontSize={"1rem"}>
              Today’s Follow Up (20)
            </Text>
          </Flex>
          <Box>
            <InputGroup>
              <InputLeftElement pointerEvents="none">
                <SearchIcon color="gray.400" mb="4" />
              </InputLeftElement>
              <Input
                value={search}
                onChange={handleSearchChange}
                borderRadius={"0.25rem"}
                borderColor="gray.400"
                size="xs"
                type="text"
                placeholder="Search here..."
              />
            </InputGroup>
          </Box>
        </Box>
        <Box overflowX="auto" className="shrinkable-table">
          <TableContainer className="tarun" whiteSpace={"nowrap"}>
            <Box overflowY="auto" height={350} maxHeight="350px">
              <Table colorScheme="gray" size="sm">
                <Thead
                  bg={"blue.600"}
                  color={"whiteAlpha.900"}
                  position={"sticky"}
                  borderRadius={"20px"}
                  top="0"
                >
                  <Tr>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      UID
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Ass To
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Source
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      City
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Status
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Enq. Date
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Lfd
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Name
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Mob_1
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      course
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Price
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Days
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      follow up
                    </Th>
                    <Th
                      padding={2}
                      w={15}
                      fontSize={"0.65rem"}
                      color={"whiteAlpha.900"}
                    >
                      Action
                    </Th>
                  </Tr>
                </Thead>
                {isLoading ? (
                  <Loader />
                ) : (
                  <Tbody>
                    {filteredData?.map((el: any) => (
                      <Tr
                        key={el._id}
                        fontSize={"0.5rem"}
                        bg={
                          el.Status === "Final Lead"
                            ? "green.100"
                            : el.Status === "Open"
                            ? "blue.200"
                            : el.Status === "Hot Lead"
                            ? "yellow.200"
                            : el.Status === "Close Lead"
                            ? "red.200"
                            : el.Status === "Enrolled"
                            ? "whiteAlpha.300"
                            : ""
                        }
                      >
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.UID}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.AssignTo}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.Source}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.City}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.Status}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {new Date(el.EnquiryDate).toLocaleDateString()}
                        </Td>
                        <Td
                          fontSize={"0.8rem"}
                          textAlign={"center"}
                          padding={2}
                          w={15}
                        >
                          {el.Days2}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.Name}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.Phone1}
                        </Td>
                        <Td
                          fontSize={"0.8rem"}
                          padding={2}
                          w={15}
                          textOverflow={"ellipsis"}
                          overflow={"hidden"}
                          whiteSpace={"nowrap"}
                        >
                          {/* {el.Course.length > 10 ? `${el.Course.slice(0, 10)}...` : el.Course} */}
                          {el.EnquiryCourse}
                        </Td>

                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {el.CoursePrice}
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {" "}
                          -180
                        </Td>
                        <Td fontSize={"0.8rem"} padding={2} w={15}>
                          {new Date(el.EnquiryDate).toLocaleDateString()}
                        </Td>
                        <Td
                          textAlign={"center"}
                          fontSize={"0.8rem"}
                          // padding={2}
                          w={15}
                        >
                          {" "}
                          <TfiEye color="blue" size={18} />
                          {/* <AlertDialogDelete
                            id={el._id}
                            handleDelete={handleDelete}
                          /> */}
                        </Td>
                      </Tr>
                    ))}
                  </Tbody>
                )}
              </Table>
            </Box>
          </TableContainer>
        </Box>
        <Box
          display={"flex"}
          justifyContent={"flex-end"}
          gap={"3"}
          px={3}
          borderTop={"1px gray.500"}
        >
          <Button
            size={"xs"}
            onClick={() => handlePageChange(page - 1)}
            colorScheme="gray"
            disabled={page === 1}
            borderRadius={"50%"}
            p={"0.5rem 0"}
          >
            &lt;
          </Button>
          {Array.from({ length: totalPages }, (_, i) => i + 1).map(
            (pageNumber) => {
              if (
                pageNumber <= 3 ||
                (pageNumber >= page - 1 && pageNumber <= page + 1) ||
                pageNumber > totalPages - 3
              ) {
                return (
                  <Button
                    size={"xs"}
                    borderRadius={"50%"}
                    p={"0.5rem 0"}
                    key={pageNumber}
                    onClick={() => handlePageChange(pageNumber)}
                    disabled={page === pageNumber}
                    colorScheme={page === pageNumber ? "orange" : "gray"}
                  >
                    {pageNumber}
                  </Button>
                );
              } else if (pageNumber === 4 || pageNumber === totalPages - 2) {
                return <Text key={pageNumber}>...</Text>;
              } else {
                return null;
              }
            }
          )}

          <Button
            size={"xs"}
            borderRadius={"50%"}
            p={"0.5rem 0"}
            onClick={() => handlePageChange(page + 1)}
            disabled={page === totalPages}
          >
            &gt;
          </Button>
        </Box>
      </Stack>
    </Stack>
  );
};

export default UserDashboard;
